.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-image: url("try.jpg");
  background-size: cover;
  background-position: center;
  padding: 1rem;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items:center;
  justify-content: center;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: calc(15px + 2vmin);
  color: rgb(182, 31, 31);
  text-shadow: 2px 5px black;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

button{
  margin-left: 10px;
  background-color: rgb(43, 3, 3);
  border: none;
  color: rgb(211, 187, 187);
  border-radius: 5px;
  padding: 10px 25px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  font-family: 'Courier New', Courier, monospace;
}

.data{
  width: 200px;
  height: 30px;
  background-color: rgb(92, 8, 8);
  border-style:outset;
  border-color: rgb(182, 31, 31);
  border-radius: 5px;
  font-size: 16px;
  
}

.btn{
  margin-top: 5px;
  margin-left: 40px;
}